body {
	background-color: $bg-color;
	font-family: 'Raleway', sans-serif;
	font-size: 14px;
	color: $text-color; }
a {
	text-decoration: none;
	color: $text-color; }
address {
	font-style: normal; }
p {
	margin: 0; }
.container {
	@include wrapper(); }
.section-title {
	@include playfairfont();
	font-size: 36px;
	line-height: 48px;
	color: $primary;
	@include s-block() {
		font-size: 30px;
		line-height: 35px; } }
input,
submit {
	border: none; }
