.section-title_appartments {
	margin-bottom: 100px;
	@include s-block() {
		margin-bottom: 50px; } }
.appartments {
	&-block {
		@include row-flex();
		justify-content: center;
		&__item {
			position: relative;
			margin-bottom: 30px;
			min-width: 40%;
			min-height: 10vh;
			@include md-block() {
				margin-bottom: 15px; }
			&:nth-child(odd) {
				margin-right: 30px;
				@include md-block() {
					margin-right: 0; } } }
		&__image {
			max-width: 100%; }
		&__title {
			position: absolute;
			left: 50px;
			bottom: 50px;
			font-size: 24px;
			@include xs-block() {
				font-size: 18px;
				left: 20px;
				bottom: 20px; } } } }
