.footer {
	background-color: $bg-footer;
	padding-top: 50px;
	padding-bottom: 50px;
	&-block {
		@include row-flex();
		justify-content: space-between;
		@include xs-block() {
			flex-direction: column;
			align-items: center; }
		&__logo {
			align-self: flex-start;
			@include xs-block() {
				align-self: center;
				margin-bottom: 30px; } } } }
.footer-menu {
	display: flex;
	flex-direction: column;
	@include xs-block() {
		margin-bottom: 30px; }
	&__link {
		margin-bottom: 15px;
		&:hover {
			color: $primary; } } }
.contacts {
	@include xs-block();
	&__address {
		display: flex;
		margin-bottom: 15px; }
	&__phone {
		margin-bottom: 15px; }
	&__work-time {
		margin-bottom: 15px; }
	&__email {
		margin-bottom: 15px;
		&-link {
			font-weight: bold;
			color: $primary; } }
	&-socials {
		&__link {
			margin-right: 20px; } } }
