.testimonials {
	&-block {
		@include row-flex();
		max-width: 794px;
		padding-top: 115px;
		padding-bottom: 120px;
		justify-content: space-between;
		margin: 0 auto;
		@include xs-block() {
			padding-top: 50px;
			padding-bottom: 50px; }
		&__item {
			text-align: center;
			@include sm-block() {
				flex-basis: 50%;
				margin-bottom: 30px; }
			@include xs-block() {
				flex-basis: 100%; } }
		&__image {
			margin-bottom: 53px;
			@include md(margin-bottom, 15px); }
		&__text {
			@include robotofont();
			font-size: 20px;
			line-height: 27px; } } }
