.header {
	background-image: url(../img/header/head-bg.jpg);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	padding-top: 30px;
	padding-bottom: 79px; }
.navbar {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 200px;
	@include s-block() {
		margin-bottom: 100px; }
	&__menu-button {
		display: none;
		@include sm(display, block);
		cursor: pointer;
		width: 50px;
		height: 50px;
		background-color: transparent;
		border: none;
		outline: none;
		padding: 0;
		&:after,
		&:before {
			content: '';
			display: block;
			width: 30px;
			height: 2px;
			margin: 0 auto;
			margin-top: 5px;
			margin-bottom: 5px;
			background-color: $primary;
			transition: 0.2s; }
		& span {
			display: block;
			width: 30px;
			height: 2px;
			margin: 0 auto;
			background-color: $primary;
			transition: 0.2s; }
		&_active {
			& span {
				display: none; }
			&:after {
				margin: 0 auto;
				margin-top: -2px;
				transform: rotate(45deg); }
			&:before {
				margin: 0 auto;
				// margin-bottom: -2px
				transform: rotate(-45deg); } } } }

.menu {
	transform: translateY(10%);
	transition: 0.5s;
	@include sm-block() {
		display: none;
		&_active {
			position: absolute;
			display: flex;
			flex-direction: column;
			top: 86px;
			background-color: rgba($bg-color, 0.9);
			width: 100%;
			align-items: center;
			transform: translateY(0%); } }
	&__link {
		transition: 0.5s;
		&:not(:first-child) {
			margin-left: 40px; }
		&:hover {
			color: $primary; }
		@include sm-block() {
			&:not(:first-child) {
				margin-left: 0; }
			margin-bottom: 20px;
			font-size: 18px;
			&:first-child {
				margin-top: 20px; } } } }
.hero {
	@include col();
	@include size(7);
	@include size-md(12);
	margin: 0 auto;
	&__title {
		margin-bottom: 100px;
		@include playfairfont();
		font-size: 48px;
		line-height: 64px;
		text-align: center;
		color: $primary;
		@include xs-block() {
			font-size: 40px;
			line-height: 44px;
			margin-bottom: 100px; }
		@include s-block() {
			font-size: 30px;
			line-height: 35px; } }
	&__mouse {
		display: block;
		margin-right: auto;
		margin-left: auto;
		margin-bottom: 200px;
		background-image: url(../img/header/mouse.svg);
		width: 40px;
		height: 40px;
		@include xs-block() {
			margin-bottom: 100px; } }
	&__info {
		@include row-flex();
		font-size: 18px;
		@include md(justify-content, center);
		@include xs-block() {
			flex-direction: column;
			align-items: center; }
		@include s-block() {
			font-size: 16px; } }
	&__address {
		display: flex;
		margin-right: 100px;
		@include md(margin-right, 30px);
		@include xs-block() {
			margin-right: 0;
			margin-bottom: 15px; }
		&:before {
			display: block;
			content: '';
			background-image: url(../img/header/location.svg);
			width: 21px;
			height: 21px;
			margin-right: 15px; } }
	&__phone {
		display: flex;
		&:before {
			display: block;
			content: '';
			background-image: url(../img/header/phone-call.svg);
			width: 21px;
			height: 21px;
			margin-right: 15px; } } }
