$primary: #D4C17F;
$bg-color: #242B33;
$bg-footer: #2D343C;
$text-color: #FFFFFF;
$color-input: #BEBEBE;
$color-footnote: #E7E7E7;


@mixin wrapfull() {
  max-width: 1920px; }
@mixin ralewayfont() {
  font-family: 'Raleway', sans-serif; }
@mixin playfairfont() {
  font-family: 'Playfair Display', serif; }
@mixin robotofont() {
  font-family: 'Roboto', sans-serif; }
