.section-title_cta {
	margin-bottom: 50px; }
.cta {
	padding-top: 180px;
	padding-bottom: 180px;
	background-image: url(../img/cta/cta-bg.jpg);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	@include s-block() {
		padding-top: 80px;
		padding-bottom: 80px; }
	&_without-bg {
		background-image: none; }
	&-block {
		@include row-flex();
		justify-content: space-between;
		&__text {
			flex-basis: calc(50% - 15px);
			font-size: 16px;
			line-height: 24px;
			@include md-block {
				flex-basis: 100%;
				margin-bottom: 50px; } } } }
.form {
	flex-basis: calc(50% - 15px);
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	@include md-block {
		flex-basis: 100%; }
	&_qna {
		flex-basis: 100%; }
	&__input {
		flex-basis: calc(50% - 15px);
		align-self: flex-start;
		@include md-block {
			flex-basis: calc(25% - 15px); }
		@include sm-block {
			flex-basis: calc(50% - 15px);
			margin-bottom: 15px; }
		@include xs-block {
			flex-basis: 100%; }
		&_qna {
			flex-basis: calc(25% - 30px);
			@include sm-block {
				flex-basis: 100%; } } }
	&__submit {
		flex-basis: calc(50% - 15px);
		align-self: flex-end;
		@include md-block {
			flex-basis: calc(25% - 15px); }
		@include sm-block {
			flex-basis: calc(50% - 15px); }
		@include xs-block {
			flex-basis: 100%; }
		&_qna {
			flex-basis: 25%;
			@include sm-block {
				flex-basis: 100%; } } }
	&__footnote {
		flex-basis: calc(50% - 15px);
		align-self: flex-end;
		@include md-block {
			flex-basis: calc(25% - 15px); }
		@include sm-block {
			flex-basis: calc(50% - 15px); }
		@include xs-block {
			flex-basis: 100%;
			justify-content: center;
			margin-bottom: 15px; }
		&_qna {
			flex-basis: calc(25% - 30px);
			@include sm-block {
				flex-basis: 100%;
				justify-content: center;
				margin-bottom: 15px; } } } }
.input {
	padding-top: 12px;
	padding-bottom: 11px;
	line-height: 27px;
	text-align: center;
	color: $color-input;
	background-color: rgba(255, 255, 255, 0.2); }
input::placeholder {
	color: $color-input; }
.submit {
	padding-top: 12px;
	padding-bottom: 11px;
	font-size: 16px;
	line-height: 27px;
	font-weight: bold;
	color: $text-color;
	text-align: center;
	background-color: $primary;
	cursor: pointer; }
.footnote {
	display: flex;
	align-items: center;
	font-size: 13px;
	line-height: 16px;
	text-align: center;
	height: 50px; }
